.aligned-center {
  margin: auto;
}

.slick-slide,
.slick-slide * {
  outline: none !important;
}

.btn {
  text-transform: uppercase;
  display: block;
  max-width: 176px;
  text-align: center;
  text-decoration: none;
  height: 45px;
  border: 1px solid transparent;
  font: 300 15px/43px "Fira Sans Extra Condensed", sans-serif;
  letter-spacing: 0.075em;
  transition: all .3s ease-in-out;
  cursor: pointer;
  width: 100%;
}

.btn--white {
  border-color: #fff;
  color: #fff;
}

.btn--white:hover {
  background: #fff;
  color: #000;
}

.btn--gray {
  border-color: #a28d7c;
  color: #806f61;
  background: transparent;
}

.btn--gray:hover {
  background: #a28d7c;
  color: #fff;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

