.container {
  max-width: 1160px;
  padding: 0 10px;
  margin: auto;
}

.large-container {
  max-width: 1254px;
  padding: 0 58px;
  margin: auto;
  position: relative;
}

html,
body {
  height: 100%;
  font-family: "Open Sans", sans-serif;
}

.all-wrapper {
  min-height: 100%;
  margin-bottom: -144px;
}

.all-wrapper::after {
  content: "";
  display: block;
}

.site-footer,
.all-wrapper::after {
  height: 144px;
}

.site-header {
  height: 76px;
  border-bottom: 1px solid #d1d1d1;
}

.logo {
  float: left;
  margin-top: 15px;
  display: block;
}

.logo img {
  max-width: 100%;
  height: auto;
  display: block;
}

.main-menu {
  float: left;
  list-style-type: none;
  margin: 27px 0 0 10px;
  padding: 0;
}

.main-menu li {
  display: inline-block;
  padding: 0 20px;
  border-right: 2px solid #888888;
}

.main-menu li:last-child {
  border: none;
}

.main-menu li a {
  font: 300 15px "Fira Sans Extra Condensed", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  letter-spacing: 0.075em;
}

.main-menu li a:hover {
  text-decoration: underline;
}

.main-menu select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  outline: 0;
  background: transparent;
  font: 300 15px "Fira Sans Extra Condensed", sans-serif;
  text-transform: uppercase;
  padding: 0 15px;
  cursor: pointer;
}

.main-menu .lang {
  background: url(../img/select-arr.png) left 5px center no-repeat;
  padding: 0;
}

.tel-side {
  float: right;
  margin-top: 23px;
}

.tel-side .tel {
  font: 21px "Open Sans", sans-serif;
  color: #806f61;
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
}

.tel-side__social {
  display: inline-block;
  vertical-align: middle;
  margin: 4px 0 0 10px;
  padding: 0;
  list-style-type: none;
}

.tel-side__social li {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center center;
}

.tel-side__social li a {
  display: block;
  height: 100%;
}

.tel-side__social li:hover {
  opacity: .8;
}

.top-block {
  background: url(../img/top-bg.jpg) top center no-repeat;
  background-size: cover;
  height: calc(100vh - 76px);
  display: flex;
  justify-content: center;
  color: #fff;
  align-items: center;
  position: relative;
}

.top-block__inner {
  padding: 0 170px;
  text-align: center;
}

.top-block__inner img {
  display: block;
  margin: 0 auto 60px;
  max-width: 100%;
  height: auto;
}

.top-block__inner h1 {
  font: 100 90px "Fira Sans Extra Condensed", sans-serif;
  margin: 0 0 20px;
}

.top-block__inner p {
  font: 300 19px/28px "Open Sans", sans-serif;
  margin-bottom: 35px;
}

.top-block__inner .btn {
  margin: auto;
}

.top-block .to-bottom {
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -12px;
  width: 24px;
  height: 13px;
  background: url(../img/to-bottom.png) no-repeat;
}

.tabs-block {
  background: #fafafa;
  border-bottom: 1px solid #d9d9d9;
}

.tabs-block__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 28px 0;
}

.tabs-block__item {
  width: 16.666%;
  border-right: 1px solid #ebebeb;
  border-left: 1px solid #fff;
  padding: 20px 0;
  position: relative;
  cursor: pointer;
}

.tabs-block__item::after {
  content: '';
  background: url(../img/tab-bottom.png) no-repeat;
  width: 25px;
  height: 17px;
  position: absolute;
  bottom: -40px;
  left: 50%;
  margin-left: -12px;
  display: none;
}

.tabs-block__item:hover::after,
.tabs-block__item.active::after {
  display: block;
}

.tabs-block__item img {
  display: block;
  margin: auto;
}

.tabs-block__item .heading {
  display: block;
  text-align: center;
  margin-top: 10px;
  font: 14px "Open Sans", sans-serif;
  color: #666666;
}

.tabs-block__item .img-wrap {
  height: 50px;
}

.tabs-block__item:first-child {
  border-left: none;
}

.tabs-block__item:last-child {
  border-right: none;
}

.tabbed__slider {
  overflow: hidden;
  padding: 70px 0;
}

.tabbed__slider .slick-list {
  margin: 0 -20px;
}

.tabbed__slider .slick-slide {
  margin: 0 20px;
}

.tabbed__slider-item {
  display: none;
}

.tabbed__slider-item.active {
  display: block;
}

.fourth-block {
  width: 25%;
}

.sl-arr {
  width: 16px;
  height: 41px;
  position: absolute;
  top: 50%;
  margin-top: -20px;
  cursor: pointer;
}

.sl-arr.sl-prev {
  left: 0;
  background: url(../img/sl-prev.png) no-repeat;
}

.sl-arr.sl-next {
  right: 0;
  background: url(../img/sl-next.png) no-repeat;
}

.small-block .img-wrap {
  height: 250px;
  background-size: cover;
  background-position: center center;
}

.small-block a {
  display: block;
  text-decoration: none;
}

.small-block img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

.small-block span {
  display: block;
}

.small-block .heading {
  font: 300 21px "Fira Sans Extra Condensed", sans-serif;
  color: #806f61;
  margin-top: 20px;
}

.small-block .subheading {
  font: 14px "Open Sans", sans-serif;
  color: #999999;
  margin: 10px 0;
}

.small-block .small-text {
  font: 14px "Open Sans", sans-serif;
  color: #404040;
}

.small-block:hover .small-text,
.small-block:hover .heading {
  text-decoration: underline;
}

.top-positions {
  background: #fafafa;
  padding: 80px 0;
  margin-top: 50px;
}

.top-positions__inner {
  display: flex !important;
  align-items: center;
}

.top-positions__col {
  width: 50%;
}

.top-positions__col img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

.top-positions .slick-dots {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.top-positions .slick-dots li {
  display: inline-block;
  width: 6px;
  height: 6px;
  margin: 0 2px;
}

.top-positions .slick-dots li button {
  text-indent: -9999px;
  font-size: 0px;
  outline: 0;
  background: #d9d9d9;
  border-radius: 50%;
  display: block;
  height: 100%;
  padding: 0;
  border: none;
  width: 100%;
  cursor: pointer;
}

.top-positions .slick-dots li.slick-active button {
  background: #a28d7c;
}

.item-cart-descr .heading {
  font: 100 60px "Fira Sans Extra Condensed", sans-serif;
  color: #000;
  margin: 0;
}

.item-cart-descr .tags {
  margin: 20px 0;
  font: 14px "Open Sans", sans-serif;
  color: #999999;
}

.item-cart-descr .description {
  font: 400 16px/24px "Open Sans", sans-serif;
  color: #404040;
  margin: 0 0 30px;
}

.index-positions__item {
  background: #faf7f5;
  padding: 140px 0;
  position: relative;
}

.index-positions__item:nth-child(2n) .index-positions__img {
  right: auto;
  left: 0;
}

.index-positions__item:nth-child(2n) .index-positions__col {
  float: right;
  padding-right: 0;
  padding-left: 70px;
}

.index-positions__col {
  width: 50%;
  padding-right: 70px;
}

.index-positions__img {
  position: absolute;
  right: 0;
  top: 0;
  width: 50%;
  height: 100%;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

.popular-items {
  padding-bottom: 50px;
}

.popular-items .slick-slide {
  margin: 0 20px;
}

.popular-items .slick-track {
  margin: 0 -20px;
}

.simple-heading {
  margin: 50px 0;
  font: 100 60px "Fira Sans Extra Condensed", sans-serif;
  text-align: center;
}

.contacts-block {
  background: url(../img/contacts-bg.jpg) top center no-repeat;
  background-size: cover;
  padding-bottom: 50px;
}

.contacts-block .simple-heading {
  color: #fff;
  margin-bottom: 10px;
}

.contacts-block .subheading {
  font: 16px "Open Sans", sans-serif;
  color: #fff;
  text-align: center;
}

.contacts-block__form {
