  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 0 10px;
  outline: 0;
  width: 100%;
  color: #fff;
  font: 300 13px "Open Sans", sans-serif;
}

.contacts-block textarea {
  display: block;
  width: 100%;
  margin: 15px 0;
  resize: none;
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.1);
  outline: 0;
  height: 130px;
  color: #fff;
  font: 300 13px "Open Sans", sans-serif;
  padding: 5px 10px;
}

.contacts-block .file-inp {
  display: none;
}

.contacts-block label {
  color: #999796;
  font: 13px/20px "Open Sans", sans-serif;
  cursor: pointer;
  display: inline-block;
  padding-left: 25px;
  background: url(../img/file-ico.png) left top 3px no-repeat;
}

.contacts-block .file-wrap {
  text-align: center;
  margin-bottom: 30px;
  margin-top: 30px;
}

.site-footer__top {
  background: #333333;
  height: 104px;
}

.site-footer .footer-logo {
  float: left;
  margin-top: 30px;
}

.site-footer .footer-inf {
  float: left;
  font: 400 14px "Open Sans", sans-serif;
  color: #666666;
  padding-left: 20px;
  text-decoration: none;
}

.site-footer .footer-loc {
  margin: 44px 0 0 80px;
  background: url(../img/footer-loc.png) left center no-repeat;
}

.site-footer .footer-tel {
  margin: 44px 0 0 125px;
  background: url(../img/footer-tel.png) left center no-repeat;
